import React, { Component } from "react";
import Img from "gatsby-image";
import path from "path";
import "./Banner.scss";

class Banner extends Component {
  constructor(props) {
    super(props);
    this.handleButtonClick = this.handleButtonClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }

  handleKeyPress() {
    this.handleButtonClick();
  }
  handleButtonClick() {
    document.querySelector('.md-grid').scrollTop = 0;
  }
  render() {
    const { fileEdges } = this.props;
    const bannerImageNode = fileEdges.filter(
      i => {
        const m = i.node.absolutePath.indexOf("banner-image");
        return m > 0;
      }
    );
    // console.log(bannerImageNode[0].node.childImageSharp.sizes);
    return (
      <div
        className="banner"
        style={{
          backgroundImage: `url(${bannerImageNode[0].node.childImageSharp.sizes.src})`
        }}
      >
        <div className="content">
          <hgroup>
            <h1>EqualD ONEDAY CLASS<br /> × <br />CAFE LUXEMBROUG</h1>
            <p>28th - 1st, Feb. 2019</p>
          </hgroup>

        </div>
        <button className="button" onClick={this.handleButtonClick} onKeyPress={this.handleKeyPress}>
          Class List ▼
        </button>
      </div>
    )
  }
}

export default Banner;



